@import "utils";

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.4;
  color: $white;
  margin: 0;

  &::before {
    content: "";
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: -1;
    background-color: $dark-blue;
    background-image: linear-gradient(115deg, $purple1-linear, $purple2-linear),
      url(https://cdn.freecodecamp.org/testable-projects-fcc/images/survey-form-background.jpeg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
  }
}

h1 {
  font-weight: 400;
  line-height: 1.2;
}

p {
  font-size: 18px;
}

h1,
p {
  margin-top: 0;
  margin-bottom: 8px;
}

label {
  display: flex;
  align-items: center;
  font-size: 18px;
  margin-bottom: 8px;
}

input,
button,
select,
textarea {
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

button {
  border: none;
}

.container {
  width: 100%;
  margin: 50px auto 0;
}

@media (min-width: 576px) {
  .container {
    max-width: 540px;
  }
}

@media (min-width: 768px) {
  .container {
    max-width: 720px;
  }
}

.header {
  padding: 0 10px;
}

.description {
  font-style: italic;
  font-weight: 200;
  text-shadow: 1px 1px 1px $shadow-color;
}

.clue {
  margin-left: 4px;
  font-size: 16px;
}

.text-center {
  text-align: center;
}

form {
  background: $dark-blue;
  padding: 40px 10px;
  border-radius: 4px;
}

@media (min-width: 480px) {
  form {
    padding: 40px;
  }
}

.form-group {
  margin: 0 auto 20px;
  padding: 4px;
}

.form-control {
  display: block;
  width: 100%;
  height: 38px;
  padding: 6px 12px;
  color: $dark-grey;
  background-color: $white;
  background-clip: padding-box;
  border: 1px solid $dark-white;
  border-radius: 4px;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.form-control:focus {
  border-color: $light-blue;
  outline: 0;
  box-shadow: 0 0 0 3px $grey-blue;
}

.input-radio,
.input-checkbox {
  display: inline-block;
  margin-right: 10px;
  min-height: 20px;
  min-width: 20px;
}

.input-textarea {
  min-height: 120px;
  width: 100%;
  padding: 10px;
  resize: vertical;
}

.submit-button {
  display: block;
  width: 100%;
  padding: 12px;
  background: $green;
  color: inherit;
  border-radius: 2px;
  cursor: pointer;
}
